import { reactive, ref, computed } from "vue"
import { useStore } from "vuex"
// 每頁必要元素，包裝起來重複使用
export default function () {
  // mode
  const mode = ref("add")

  // 關鍵字
  const searchMap = reactive({ word: "" })

  // 開關設定
  const listLoading = ref(true)
  const dialogFormVisible = ref(false)

  // 分頁
  const pageSize = ref(10)
  const currPage = ref(1)
  const pageLayout = () => {
    if (store.getters.device !== "mobile")
      return "prev, pager, next, sizes, total"
    else return "prev, next, total"
  }

  const handleCurrentChange = (_page) => {
    currPage.value = _page
  }
  const handleSizeChange = (_size) => {
    pageSize.value = _size
  }

  const store = useStore()
  const headers = ref({
    Authorization: `Bearer ${store.getters.token}`
  })
  const uploadURL = ref(
    `${process.env.VUE_APP_BASE_URL}/api/admin/file/upload?type=image`
  )

  const hasPermission = (roles, role) => {
    return roles.some((r) => r.includes(role))
  }

  const userInfo = computed(() => store.getters.userInfo)
  return {
    searchMap,
    mode,
    listLoading,
    dialogFormVisible,
    pageSize,
    currPage,
    pageLayout,
    handleCurrentChange,
    handleSizeChange,

    headers,
    uploadURL,

    hasPermission,
    userInfo
  }
}
